import React from 'react';
import './App.css';
import NavBar from '../../Containers/NavBar/NavBar';
import FavoritesContainer from '../../Containers/FavoritesContainer/FavoritesContainer';
import Recommendations from '../../Containers/Recomendations/Recomendations';
import LocationForm from '../../Containers/LocationForm/LocationForm';
import { Route } from 'react-router-dom';
import Home from '../../Containers/Home/Home';
import Login from '../../Containers/Login/Login';
import SignUp from '../../Containers/SignUp/SignUp';
import PlacesNearYou from '../../Containers/PlacesNearYou/PlacesNearYou';

const App = () => {
  return (
    <div className="App">
      <NavBar />
      <div>
        <Route exact path='/eats/' component={Home} />
        <Route path='/eats/favorites' component={FavoritesContainer}/>
        <Route path='/eats/recommendations' component={Recommendations}/>
        <Route path='/eats/location' component={LocationForm}/>
        <Route path='/eats/login' component={Login}/>
        <Route path='/eats/signup' component={SignUp}/>
        <Route path='/eats/suggestions' component= {PlacesNearYou} />
      </div>
    </div>
  );
};

export default App;



